import GlobalSettings from '../components/Settings/GlobalSettings'
import { hillo, IKDataEntity } from 'innerken-utils'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },
  name: {
    displayName: 'reservename',
  },
  personCount: {
    displayName: 'personCount',
  },
  phone: {},
  time: {
    type: IKDataEntity.Types.Time,
  },
}
const config = {
  add: function (item) {
    item.count = item.personCount
    return hillo.postWithUploadFile('Reserve.php?op=add', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    item.count = item.personCount
    return hillo.postWithUploadFile('Reserve.php?op=update', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Reserve.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Reserve.php', {
      op: 'all',
      lang: GlobalSettings.lang(),
      ...filter,
    })).content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
